import styles from './Landing.module.css';
import "../App.css"
function Landing({primary, handleClick, message}){
    return (
        <div className={styles.Landing}>
            <h1 className={styles.primary}>{primary}</h1>
            {/* <h3 className={styles.secondary}>{secondary}</h3> */}
            {message.split("\n").map(m=><p style={{width:"80%"}}>{m}</p>)}
            <button onClick={handleClick} className="primary-btn">Start Explore</button> 
        </div>
    )
}

export default Landing;