import styles from "./NavBar.module.css";
import { memo } from "react";
function NavBar({ onPageChange }) {
    const pages = ["Home","Gallery", "Blog", "About", "Contact"];
    const itemIds = {
        "Home": "home",
        // "Live Updates": "updates",
        "Hall of Fame" : "hof",
        "Blog": "blogs",
        "About": "about",
        "Contact": "contact"
    }
    console.log("rerender");
    return (
        <div className={styles.NavBar}>
            {
                pages.map((item) =>
                    <div className={styles.NavBar__item} onClick={() => {
                        onPageChange(itemIds[item])
                    }
                    }
                    key={item}>{item}</div>
                )
            }
        </div>
    )
}

export default memo(NavBar);