import styles from "./Blog.module.css";
import client from './client'
import { memo, useEffect, useState, useRef } from "react";
import { documentToHtmlString } from '@contentful/rich-text-html-renderer';
import options from "./option";

function Blog() {

    const [mainTitle, setMainTitle] = useState("");
    // const [mainPreview, setMainPreview] = useState("");
    const [mainPreviewImg, setMainPreviewImg] = useState("");
    const [dateCreated, setDateCreated] = useState("");
    const [showFull, setShowFull] = useState(false);
    const [data, setData] = useState({
        postTitle: "",
        postDescription: "",
        blogContent: "",
        dateCreated: "",
        previewImg: ""
    });
    const [mainIdx, setMainIdx] = useState(2);
    const [allPosts, setAllPosts] = useState([]);
    const mainContentRef = useRef();
    const [postPreviews, setPostPreviews] = useState([]);

    useEffect(() => {
        // ('3KPLdwFuCExElALyKUNZ0E')
        client.getEntries().then(function (entry) {
            const posts = entry.items.map(
                (item, index) => {
                    const post = {
                        postIndex: index,
                        postTitle: item.fields.postTitle,
                        postDescription: item.fields.postDescription,
                        blogContent: documentToHtmlString(item.fields.blogContent, options),
                        dateCreated: item.fields.dateCreated.slice(0, 10),
                        previewImg: item.fields.previewImage.fields.file.url
                    };

                    return post;
                }
            )

            setAllPosts(posts);

        });
    }, []);
    useEffect(() => {
        if (allPosts.length > 0) {
            setData(allPosts[mainIdx]);
            setPostPreviews(allPosts.filter((item, index) => index !== mainIdx));
        }
    }, [allPosts, mainIdx]);

    useEffect(() => {

        setMainTitle(data.postTitle);
        setDateCreated(data.dateCreated);
        setMainPreviewImg(data.previewImg);

    }, [data])

    useEffect(() => {
        if (showFull) {
            mainContentRef.current.innerHTML = data.blogContent;
        } else {
            mainContentRef.current.innerHTML = data.postDescription;
        }
    }, [showFull, data]);

    console.log("rerender");

    function showFullPost() {
        setShowFull(prev => !prev);
    }
    function handleChangeMainIdx(id) {
        setMainIdx(id);
        setShowFull(true);
        window.scrollTo(0, 0);
    }

    return (
        <div className={styles.Blog}>
            {(allPosts.length > 0) ? (<><div className={styles.mainPost}>
                <img src={mainPreviewImg} alt="mainImg" />
                <h1 onClick={showFullPost}>{mainTitle}</h1>
                <span style={{ backgroundColor: "#999", color: "#fff", padding: "3px" }}>{dateCreated}</span>
                <p ref={mainContentRef}></p>
            </div>
                <div className={styles.secondaryPosts}>
                    {postPreviews.map(preview =>
                        <div className={styles.postPreview} key={preview.title} onClick={() => handleChangeMainIdx(preview.postIndex)}>
                            <img src={preview.previewImg} alt="preview" />
                            <h2>{preview.postTitle.slice(0, 30)}</h2>
                            <p>{preview.postDescription.slice(0, 150)}</p>
                        </div>
                    )}
                </div> </>) : (<><img src="assets/loading.gif" alt="loading" style={{ position: "absolute", top: "50%", left: "50%", width: "4rem", objectFit: "cover" }} /><p ref={mainContentRef}></p></>)}
        </div>
    )
}

export default memo(Blog);